import * as d3 from 'd3';
import { useD3 } from '../../hooks/useD3';
import { useWindowSize } from '../../hooks/useWindowSize';
import {
  FOOD_NEEDED_BY_INDIVIDUAL,
  MAX_FOOD_WASTE_IN_TONS,
  MAX_UNDERNOURISHED_POPULATION,
  ZERO_HUNGER_POINT,
} from './constants';
import {
  MIN_WINDOW_WIDTH_FOR_ROW_FLEX,
  MOBILE_WIDTH
} from '../../container/constants';

const STARVATION_INFO_CHART_D3_X_AXIS_ID = 'starvation-info-chart-d3-x-axis';
const STARVATION_INFO_CHART_D3_Y_AXIS_ID = 'starvation-info-chart-d3-y-axis';
const STARVATION_INFO_CHART_D3_BAR_CHART = 'starvation-info-chart-d3-bar-chart';

// const DEFAULT_DIMS = { height: 300, width: 600, radius: 150};
const margin = ({top: 30, right: 0, bottom: 30, left: 80});
// const graphWidth = dims.width - margin.left - margin.right;
// const graphHeight = dims.height - margin.top - margin.bottom;

const StarvationInfoChart = ({ foodWasteInTons }) => {
  const windowSize = useWindowSize();
  const reactiveSize = windowSize.width / 2 - 100
  const isNotMobile = windowSize.width > MIN_WINDOW_WIDTH_FOR_ROW_FLEX;
  const dims = isNotMobile ? {
    height: reactiveSize / 2,
    width: reactiveSize,
  } : {
    height: MOBILE_WIDTH / 2,
    width: MOBILE_WIDTH
  }

  const drawFoodWasteChart = (svg) => {
    const undernourishedPopulation = foodWasteInTons > ZERO_HUNGER_POINT ? 
      MAX_UNDERNOURISHED_POPULATION - ((MAX_FOOD_WASTE_IN_TONS - foodWasteInTons) / FOOD_NEEDED_BY_INDIVIDUAL) :
      0;
    const surplusFood = foodWasteInTons > ZERO_HUNGER_POINT ? 0 : ZERO_HUNGER_POINT - foodWasteInTons;
    const data = [
      {
        name: '음식물 쓰레기 양',
        value: foodWasteInTons,
        color: 'orange'
      },
      {
        name: '굶주린 사람들',
        value: undernourishedPopulation,
        color: 'gray'
      },
      {
        name: '남는 식량',
        value: surplusFood,
        color: 'green'
      }
    ];

    const x = d3.scaleBand()
      .domain((data.map(d => d.name)))
      .range([margin.left, dims.width - margin.right])
      .padding(0.2);

    const y = d3.scaleLinear()
      .domain([0, MAX_FOOD_WASTE_IN_TONS + 100000000])
      .nice()
      .range([dims.height - margin.bottom, margin.top])

    const xAxis = g => g
      .attr('transform', `translate(0, ${dims.height - margin.bottom})`)
      .call(d3.axisBottom(x).tickSizeOuter(0))

    const yAxisFunc = isNotMobile ? d3.axisLeft(y) : d3.axisLeft(y).ticks(7, 'f')
    const yAxis = g => g
      .attr('transform', `translate(${margin.left}, 0)`)
      .call(yAxisFunc)
      .call(g => g.select('.domain').remove());


    svg.select(`#${STARVATION_INFO_CHART_D3_X_AXIS_ID}`).call(xAxis);
    svg.select(`#${STARVATION_INFO_CHART_D3_Y_AXIS_ID}`).call(yAxis);
    
    svg.select(`#${STARVATION_INFO_CHART_D3_BAR_CHART}`)
      // .selectAll('rect').data(data).enter().append('rect')
      .selectAll('.bar')
      .data(data)
      .join('rect')
      .attr('class', 'bar')
      .attr('fill', d => d.color)
      .attr('x', d => x(d.name))
      .attr('y', d => y(d.value))
      .attr('height', d => y(0) - y(d.value))
      .attr('width', x.bandwidth());
  };

  const ref = useD3(
    (svg) => {
      drawFoodWasteChart(svg);
    },
    [foodWasteInTons, windowSize.width]
  );

  return (
    <div>
      <svg ref={ref} width={dims.width} height={dims.height}>
        <g id={STARVATION_INFO_CHART_D3_X_AXIS_ID}></g>
        <g id={STARVATION_INFO_CHART_D3_Y_AXIS_ID}></g>
        <g id={STARVATION_INFO_CHART_D3_BAR_CHART}></g>
      </svg>
    </div>
  );
};

export default StarvationInfoChart;
