import { useState } from 'react';
import StarvationInfoChart from './StarvationInfoChart';
import StarvationInfoVizController from './StarvationInfoVizController';

const StarvationInfoViz = () => {
  const [foodWasteInTons, setFoodWasteInTons] = useState(1300000000);

  return (
    <div>
      
      <StarvationInfoChart foodWasteInTons={foodWasteInTons} />
      <StarvationInfoVizController
        foodWasteInTons={foodWasteInTons}
        setFoodWasteInTons={setFoodWasteInTons}
      />
    </div>
    
  );
};

export default StarvationInfoViz;
