  //         .attr('stroke-width', 3);
  //     }

  //     update(health)

  //     // update.enter()
  //     //   .append('text')
  //     //   .attr('x', (d, i) => i * 25)
  //     //   .attr('y', 40)
  //     //   .style('font-size', 24)
  //     //   .text(d => d)

  //     // update
  //     //   .attr('x', (d, i) => i * 40)
  //     //   .text(d => d);

  //     // update.exit()
  //     //   .remove();
  //   }
