import Slider from '@material-ui/core/Slider';
import { makeStyles } from '@material-ui/core/styles';
import { MAX_FOOD_WASTE_IN_TONS } from './constants';

const useStyles = makeStyles({
  root: {
    color: 'white',
    colorPrimary: 'white',
  },
})

const marks = [
  {
    value: 0,
    label: '0T'
  },
  {
    value: MAX_FOOD_WASTE_IN_TONS,
    label: '1.3T'
  }
];

const StarvationInfoVizController = ({ foodWasteInTons, setFoodWasteInTons }) => {
  const classes = useStyles();
  const handleChange = (event, newValue) => {
    setFoodWasteInTons(newValue);
  };

  return (
    <Slider 
      className={classes.root}
      value={foodWasteInTons}
      min={0}
      step={1}
      max={MAX_FOOD_WASTE_IN_TONS}
      marks={marks}
      onChange={handleChange}
    />
  );
};

export default StarvationInfoVizController;

