import { useWindowSize } from '../hooks/useWindowSize';
import SimpleContainer from './SimpleContainer';
import { MIN_WINDOW_WIDTH_FOR_ROW_FLEX } from './constants';

const InfoContainer = ({backgroundColor, color, flex, left, right, flexLeft, flexRight}) => {
  const size = useWindowSize();
  const flexDirection = size.width > MIN_WINDOW_WIDTH_FOR_ROW_FLEX ? 'row' : 'column';

  return (
    <SimpleContainer backgroundColor={backgroundColor} flex={flex} flexDirection={flexDirection} color={color}>
      <SimpleContainer flex={flexLeft}>
        {left}
      </SimpleContainer>
      <SimpleContainer flex={flexRight}>
        {right}
      </SimpleContainer>
    </SimpleContainer>
  );
}
  

export default InfoContainer;
